import Vue from 'vue'
import Router from 'vue-router'
import Meta from 'vue-meta'

Vue.use(Router)
Vue.use(Meta)

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [{
      path: '/',
      component: () => import('./views/Index.vue')
    },
    {
      path: '/about',
      component: () => import('./views/About.vue')
    },
    {
      path: '/careers',
      component: () => import('./views/Careers.vue')
    },
    {
      path: '/contact',
      component: () => import('./views/Contact.vue')
    },
    {
      path: '/industries',
      component: () => import('./views/industries/Index.vue'),
      children: [{
          path: 'shopping-malls',
          component: () => import('./views/industries/ShoppingMalls.vue')
        },
        {
          path: 'events',
          component: () => import('./views/industries/Events.vue')
        },
        {
          path: 'out-of-home-media',
          component: () => import('./views/industries/OutOfHomeMedia.vue')
        },
        {
          path: 'sales-galleries',
          component: () => import('./views/industries/SalesGalleries.vue')
        },
        {
          path: 'retail',
          component: () => import('./views/industries/Retail.vue')
        },
        {
          path: 'smart-cities',
          component: () => import('./views/industries/SmartCities.vue')
        }
      ]
    },
    {
      path: '/redirect/:key',
      component: () => import('./components/Redirector.vue'),
      props: true
    },
    {
      path: '*',
      component: () => import('./views/404.vue')
    }
    // {
    //   path: '/about',
    //   name: 'about',
    //   // route level code-splitting
    //   // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    //   component() {
    //     return import( /* webpackChunkName: "about" */ './views/About.vue')
    //   }
    // }
  ],
  scrollBehavior(to, from, savedPosition) {
    return {
      x: 0,
      y: 0
    };
  }
})

router.beforeEach((to, from, next) => {
  if (to.fullPath.startsWith('/#')) {
    next(to.fullPath.substr(2));
  } else {
    next();
  }
})

export default router
