import Vue from 'vue'
import VueAnalytics from 'vue-analytics'

import App from './App.vue'
import router from './router'

Vue.config.productionTip = false

Vue.use(VueAnalytics, {
  id: process.env.VUE_APP_GOOGLE_ANALYTICS_ID,
  router,
  debug: {
    enabled: false
  }
})

new Vue({
    router,
    render: h => h(App)
  })
  .$mount('#app')