<script>
import WindowInstanceMap from "@/components/WindowInstanceMap.js";

export default {
  data() {
    return {
      logoVertical: "/internal-assets/img/logo-vertical-grey.png",
      logoHorizontal: "/internal-assets/img/logo-horizontal-grey.png"
    };
  },
  computed: {
    logo() {
      return WindowInstanceMap.scrollY > 100
        ? this.logoHorizontal
        : this.logoVertical;
    },
    isSticky() {
      return WindowInstanceMap.scrollY > 100;
    },
    isIndexPage() {
      return this.$route.path === "/";
    }
  },
  methods: {
    track () {
      console.log('ga track in header!');
      this.$ga.page('/');
    }
  },
  mounted() {
    $("#mean_menu_nav").meanmenu({
      meanMenuContainer: ".mobile_menu",
      meanScreenWidth: "991"
    });
  }
};
